/* TIMELINE */
.timeline {

  .step {
    position: relative;

    padding: 7px;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    //background-color: cream;

    &:hover {
      .circle {
        background: #0696D7;
        box-shadow: none;
      }

      .line {
        background: lighten(#0696D7, 25%);
      }

      .content {
        box-shadow: inset 0 0 4px #0696D7;
        background: #fff;
      }

      .time-scope {
        color: #0696D7;
      }
    }

    .time-scope {
      color: #777;
      font-size: 12px;
      position: absolute;
      left: -40px;
      top: 50%;
      margin-top: -5px;
      //max-width: 45px;
    }
  }

  .v-stepper {
    position: relative;
    /*   visibility: visible; */
  }


  /* regular step */
  .step .circle {
    background-color: white;
    //border: 2px solid $defcolor;
    box-shadow: 0 0 3px darken($defcolor, 25%);
    border-radius: 100%;
    width: 20px;    /* +6 for border */
    height: 20px;
    display: inline-block;
    //line-height: 20px;
    //padding: 0 5px;
  }

  .step .line {
    top: 20px;
    left: 7px;
    /*   height: 120px; */
    height: 100%;

    position: absolute;
    border-left: 0;
    box-shadow: inset 0 0 3px $defcolor;
    background: #eee;
    width: 7px;
  }

  .step.completed .circle {
    visibility: visible;
    background-color: #0696D7;
    //border-color: #0696D7;
    box-shadow: none;
  }

  //.step.completed .line {
  //  border-left: 3px solid #0696D7;
  //}

  .step.active .circle {
    visibility: visible;
    border-color: #0696D7;
  }

  .step.empty .circle {
    visibility: hidden;
  }

  .step.empty .line {
    /*     visibility: hidden; */
    /*   height: 150%; */
    top: 0;
    height: 150%;
  }


  .step:last-child .line {
    //border-left: 3px solid white;
    background: #fff;
    box-shadow: none;
    z-index: -1; /* behind the circle to completely hide */
  }

  .content {
    margin-left: 20px;
    //margin-top: -20px;
    margin-bottom: 40px;
    display: inline-block;

    border-radius: 5px;
    background: #fdfdfd;
    max-width: 600px;
    padding: 20px;
    //border: 1px solid #eee;
    box-shadow: inset 0 0 3px #ccc;
    color: #777;

    .title {
      font-size: 20px;
      color: #000;
    }

    .desc {
      margin-top: 20px;
      font-size: 14px;
      color: #555;
    }

    .arrow_box {
      position: relative;
      background: #fdfdfd;
      box-shadow: inset 0 0 3px #ccc;
      border: 0;
      //border: 4px solid #c2e1f5;
    }
    .arrow_box:after, .arrow_box:before {
      right: 100%;
      top: 50%;
      border: solid transparent;
      content: "";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
    }

    .arrow_box:after {
      border-color: rgba(255, 255, 255, 0);
      border-right-color: #ffffff;
      border-width: 15px;
      margin-top: -15px;
    }
    .arrow_box:before {
      border-color: rgba(194, 225, 245, 0);
      border-right-color: #c2e1f5;
      border-width: 18px;
      margin-top: -18px;
    }
  }
}

.calculator {
  .nav {
    .nav-item {
      .nav-link {
        padding: 8px 15px;
        margin: 0 15px;
        color: #777;

        &.active,&.active:hover {
          cursor: default;
          background: #fff;
          color: #000;
        }

        &:hover {
          background: #f0f0f0;
          color: #000;
        }

        img {
          width: 40px;
          margin-right: 10px;
        }
      }

    }
  }

  .calculator-block {
    padding: 20px;

    .preview {
      margin-left: -15px;
      position: relative;
      width: 500px;
      //height:500px;
      max-width: 100%;
      background: #fff;
      //box-shadow: inset 0 0 3px #555;
      border: 1px solid #ddd;
      display: none;

      &.show {
        display: block;
      }

      .size {
        position: absolute;
        font-size: 12px;
        font-weight: bold;
        padding: 5px;
        background: #fff;
        color: #1d78cb;
      }
    }

    .calc-input {
      height: 40px;
    }

    .calculator-form {
      margin-top: 50px;

      display: none;

      &.show {
        display: block;
      }
    }

    .calculator-sum {
      margin-top: 20px;
      border-top: 1px solid #ccc;
      padding-top: 20px;
      color: #777;


      span {
        color: #000;
        margin-left: 15px;
        font-size: 70px;
        //color: #1d78cb;
      }
    }
  }
}


@media only screen and (min-width: 1280px)  {
  .stepper {
    margin-left: -32px;
    left: 50%;
    position: relative;

    .step {
      max-width: 600px;
      position: relative;

      .content {
        margin-bottom: 0px;
        margin-top: 0px;
      }
    }

    .step:nth-child(2n) {
      text-align: right;
      margin-left: -600px;

      .v-stepper {
        position: absolute;
        top: 0;
        bottom: 0;
        right: -27px;
      }

      .time-scope {
        left: auto;
        right: -70px;
      }
    }
  }
}
